.game-library
    width: 100%
    display: flex
    flex-direction: column
    gap: 0.5rem

.game-library-item
    display: flex
    flex-direction: row
    gap:2rem
    padding:1rem 2rem 
    border-radius: 10px
    background-color: rgb(148 193 236)
    user-select: none
    box-shadow: 0px 0px 3px 1px rgb(124 128 129)
    opacity: 0.9
    z-index: 200

.game-library-item-context
    width: 100%

.game-library-item-context h3
    font-size: 1.5rem
    margin: 0px

.game-library-item-text
    margin-top: 1rem

.game-library-tool-bar
    user-select: none
    font-size: 1.2rem
    padding-bottom: 1rem
    display: flex
    flex-direction: row

.game-library-tool-bar-item
    color: darkgray

.game-library-tool-bar-item:hover
    cursor: pointer
    text-decoration: underline
    color: grey

.game-library-item-image 
    max-width: 15rem

.game-library-item-image img
    width: -webkit-fill-available
    border-radius: 7px
    box-shadow: 0px 0px 11px 1px rgb(124 128 129)

.game-library-item-goto-box
    display: flex
    flex-direction: column
    justify-content: center

.game-library-item-goto-box button
    height: 110px
    width: -webkit-fill-available
    font-size: 2rem
    font-weight: bold 
    text-align: center
    color: #c09ddd
    background: url(./media/images/billy-manit-static.jpg) center
    border-radius: 15px
    cursor: pointer
    overflow: hidden
    opacity: 0.9

.game-library-item-goto-box button:hover
     background: url(./media/images/billy-manit.gif) center

@media screen and (max-width:768px)
    .game-library-item
        padding: 0.75rem 1.5rem
        overflow: hidden
    .game-library-item-image
        display: none
    .game-library-item-goto-box button
        height: 100px
        font-size: 1.3rem
    .game-library-tool-bar
        font-size: 1rem
    .game-library-item-context h3
        font-size: 1.2rem
    .game-library-item-text
        margin-top: 0.75rem
        font-size: 0.9rem

@media screen and (prefers-color-scheme: dark)
    .game-library-item
        background-color: rgb(38 89 139)
        box-shadow: 0px 0px 3px 1px rgb(61 102 112)