.gotodungeon
    margin-top: 15px
    margin: auto
    width: 200px
    height: 60px
    border: 3px solid
    border-radius: 8px
    background-color: black
    color: rgb(153, 0, 255)
    font-size: 20px
    font-weight: 650
    cursor: pointer

.gotodungeon:hover
    background-color: white
    opacity: 0.9

.gotodungeon:active
    background-color: silver

.OhSHitIamSorry
    padding: 1rem 2rem
    border: 3px solid
    text-align: center
    border-radius: 8px
    background-color: black
    color: rgb(153, 0, 255)
    font-size: 20px
    font-weight: 650
    cursor: pointer

.OhSHitIamSorry:hover
    background-color: white
    opacity: 0.9

.OhSHitIamSorry:active
    background-color: silver