.tic-tac-toe-result
    position: absolute
    width: 400px
    height: 556px
    opacity: 0.95
    background-size: 400px
    background-repeat: no-repeat 
    background-position: center
    display: flex
    justify-content: center
    align-items: center
    cursor: pointer
    z-index: 1000

.ai-win
    background-image: url('./media/images/ticTacToe/AIWin.jpg')

.user-win
    background-image: url('./media/images/ticTacToe/userWin.jpg')

.spare 
    background-image: url('./media/images/ticTacToe/spare.gif')

.tic-tac-toe-result-text
    padding-top: 10rem
    color: #1b92c9
    user-select: none 
    font-size: 90px
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif

.game-board
    justify-items: center
    display: grid
    overflow: hidden
    grid-template-columns: repeat(3, 1fr)

.game-board button
    margin: 15px
    width: 90px
    height: 150px
    cursor: pointer

.ai-captured
     background: url('./media/images/ticTacToe/aiBox.png') center no-repeat grey

.user-captured
    background: url('./media/images/ticTacToe/userBox.png') center no-repeat grey

.not-captured
    background: url('./media/images/ticTacToe/emptyBox.jpg') center no-repeat grey

.user-turn
    background: url('./media/images/ticTacToe/userTurn.gif') center no-repeat
    position: absolute
    width:90px
    height:150px
    z-index: 998

.ai-turn
    background: url('./media/images/ticTacToe/aiTurn.gif') center no-repeat
    position: absolute
    width:90px
    height:150px
    z-index: 998

