.Menu
    user-select: none 
    left: 0
    position: fixed
    width: -webkit-fill-available
    height: 55px
    top:0%
    z-index: 100
    background-color: black
    display: flex
    flex-direction: row
    align-items: center
    font-size: 22px
    font-weight: 400
    color: white
    text-align: center
    padding: 0rem 0.3rem

.MenuLeftBar
    display: flex
    flex-direction: row
    align-items: center
    width: 100%


.MenuHeader
    color: white
    font-size: 25px
    text-align: center
    margin: 0rem 0.3rem

.MenuSelection 
    margin: 0 7px 0 7px 

.MenuSelection:hover
    background: repeating-linear-gradient(crimson, orange, yellow, green,blue,purple)
    animation: MenuSelection1 11s infinite
    -webkit-background-clip: text
    -webkit-text-fill-color: transparent
    cursor: pointer

.MenuSelection:active
    color: grey
    cursor: pointer


@keyframes MenuSelection1
    100% 
        background-position: 0 200px

.MenuRightBar
    display: flex
    flex-direction: row
    align-items: center
        
.MenuRightBarItem
    display: flex
    flex-direction: row
    margin:0 7px 0 7px 

.MenuLabel
    margin:0 7px 0 0 

.mobile-menu
    display: none

.mobile-menu-content
    display: none
    position: absolute
    top: 100%
    right: 0px
    width: -webkit-fill-available
    background: black
    
.mobile-menu-content li
    border-bottom: 1px solid white
    list-style-type: none
    padding: 1.2rem 0.5rem

.mobile-menu-content ul
    margin: 0px
    padding: 0px
    font-size: 2.2rem

.mobile-menu-content ul > li:first-child
    border-top: 1px solid white

.mobile-menu:hover
    .mobile-menu-content
        display: block

@media screen and (max-width:1750px) 
    .MenuRightBar 
        display: none 

@media screen and (min-width:0px) and (max-width: 500px) 
    .mobile-menu
        display: block
        cursor: pointer
    .MenuSelection
        display: none